
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.no-scrollbar {
  overflow-y: auto;
}
.no-scrollbar::-webkit-scrollbar {
    display: none;
}
.no-scrollbar-holizontal {
  overflow-x: auto;
}
.no-scrollbar-holizontal::-webkit-scrollbar {
    display: none;
}
.piano-key.scale-tone {
  width: 100%;
  background-color: #f0f2f3;
}
.piano-key.non-scale-tone {
  width: 41px;
  background-color: #2a2f32;
}
#app.flag-collapse .piano-key.non-scale-tone {
    height: 0 !important;
}
.piano-key.note-on {
  background-color: #331933;
}
.pitch-row-wrapper {
  overflow: visible;
}
#app.flag-collapse .pitch-row-wrapper.non-scale-tone {
    height: 0 !important;
}
.pitch-row-wrapper > .pitch-row {
  overflow: visible;
}
.pitch-row-wrapper.scale-tone > .pitch-row {
  background-color: #2a2d31;
}
#app:not(.flag-collapse) .pitch-row-wrapper.non-scale-tone +
.pitch-row-wrapper.scale-tone +
.pitch-row-wrapper.non-scale-tone +
.pitch-row-wrapper.scale-tone +
.pitch-row-wrapper.non-scale-tone +
.pitch-row-wrapper.scale-tone + .pitch-row-wrapper.scale-tone > .pitch-row {
    -webkit-box-shadow: inset 0px 1px 0px #232527;
            box-shadow: inset 0px 1px 0px #232527;
}
#app:not(.flag-collapse) .pitch-row-wrapper.scale-tone +
.pitch-row-wrapper.scale-tone +
.pitch-row-wrapper.non-scale-tone +
.pitch-row-wrapper.scale-tone +
.pitch-row-wrapper.non-scale-tone +
.pitch-row-wrapper.scale-tone + .pitch-row-wrapper.scale-tone > .pitch-row {
    -webkit-box-shadow: inset 0px 1px 0px #18191b;
            box-shadow: inset 0px 1px 0px #18191b;
}
#app.flag-collapse
.pitch-row-wrapper.scale-tone + .pitch-row-wrapper.scale-tone > .pitch-row {
    -webkit-box-shadow: inset 0px 1px 0px #232527;
            box-shadow: inset 0px 1px 0px #232527;
}
.pitch-row-wrapper.non-scale-tone > .pitch-row {
  background-color: #232527;
  position: relative;
}
#app:not(.flag-collapse) .pitch-row-wrapper.non-scale-tone > .pitch-row {
    top: 0;
}
#app.flag-collapse .pitch-row-wrapper.non-scale-tone > .pitch-row {
    top: -2px;
    height: 4px !important;
}
.pitch-row-wrapper.central-tone > .pitch-row {
  background-color: #1a303c;
}
.pitch-row-wrapper.root-tone > .pitch-row {
  background-color: black;
}
.pitch-row-wrapper.note-on > .pitch-row {
  background-color: #331933;
}
.pitch-row-wrapper.scale-tone .note {
  background-color: #3e899b;
  color: white;
}
.pitch-row-wrapper.scale-tone .note.selected {
  background-color: #e8a561;
  color: black;
}
.pitch-row-wrapper.non-scale-tone .note {
  background-color: #f71f1f;
  color: white;
}
#app:not(.flag-collapse) .pitch-row-wrapper.non-scale-tone .note {
    border-radius: 0px;
}
#app.flag-collapse .pitch-row-wrapper.non-scale-tone .note {
    border-radius: 16px;
}
.pitch-row-wrapper.root-tone .note {
  background-color: #3e899b;
  color: white;
}
.pitch-row-wrapper.root-tone .note.selected {
  background-color: #e8a561;
  color: black;
}
.pitch-row-wrapper.note-on .note {
  background-color: #331933;
  color: white;
}
